/* nunito-sans-regular - cyrillic_latin */
@font-face {
	font-display: swap;
	font-family: 'Nunito Sans';
	font-style: normal;
	font-weight: 400;
	src: url('../fonts/nunito-sans/nunito-sans-v15-cyrillic_latin-regular.woff2') format('woff2');
}
